import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import './App.css';

import Quote from './components/Quote';

class App extends Component {

  constructor() {
    super();
    this.state = {
      content: null,
      author: null,
      colors: {
        background: colors[0].backgroundColor,
        text: colors[0].textColor
      }
    }
  }

  handleNewQuoteClick() {
    this.getQuote();
    this.setNewColors();
  }

  getQuote() {
    const url = 'https://talaikis.com/api/quotes/random/';
    fetch(url)
      .then(response => response.json())
      .then(responseJson => {
        this.setState({
          content: responseJson.quote,
          author: responseJson.author
        });
      });
  }

  hexToRgb(hex) {
      let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
      return result ? {
          r: parseInt(result[1], 16),
          g: parseInt(result[2], 16),
          b: parseInt(result[3], 16)
      } : null
  }

  getTextColorFor(backgroundColor) {
    const light = '#FFFFFF';
    const dark = '#212121';
    if (this.isDarkColor(backgroundColor)) {
      return dark;
    }
    return light;
  }

  getRandomColor() {
    var letters = '0123456789ABCDEF';
    var color = '#';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  setNewColors() {
    let background = this.getRandomColor();
    let text = this.getTextColorFor(background);

    this.setState({
      colors: {
        background: background,
        text: text
      }
    });
    
  }

  isDarkColor(colorToCheck) {
    let color = this.hexToRgb(colorToCheck);
    return (color.r*0.299 + color.g*0.587 + color.b*0.114) > 186;
  }
  
  componentWillMount() {
    this.getQuote();
  }
  
  render() {
    return (
      <div className="container">
        <Helmet>
          <style>{'body { background-color: ' + this.state.colors.background + '; }'}</style>
        </Helmet>
        <Quote 
              fontColor={this.state.colors.text}
              author={this.state.author}
              content={this.state.content}/>
        <span className="newQuote" style={{color: this.state.colors.text}}onClick={() => this.handleNewQuoteClick()}>
          Show me another quote
        </span>
      </div>
     
    );
  }
}


const colors = [
  {
    backgroundColor: '#4286f4',
    textColor: '#fff'
  },
  {
    backgroundColor: '#f4ce42',
    textColor: '#000'
  },
  {
    backgroundColor: '#f4a041',
    textColor: '#000'
  },
  {
    backgroundColor: '#f44141',
    textColor: '#fff'
  },
  {
    backgroundColor: '#fff',
    textColor: '#f44141'
  },
  {
    backgroundColor: '#000',
    textColor: '#f4a041'
  },
  {
    backgroundColor: '#000',
    textColor: '#f4ce42'
  },
  {
    backgroundColor: '#fff',
    textColor: '#4286f4'
  },
]

export default App;



